import {
	Box,
	Button,
	Checkbox,
	Chip,
	Divider,
	FormControl,
	InputLabel,
	ListItemText,
	MenuItem,
	Select,
	Stack,
	TextField,
	Typography,
} from '@mui/material'
import { yupResolver } from '@hookform/resolvers/yup'
import { SubmitHandler, useForm } from 'react-hook-form'
import { format } from 'date-fns'
import { ISendMailHistory, PERMISSION } from 'interfaces'
import _ from 'lodash'
import { useEffect, useMemo, useState } from 'react'
import { CgOrganisation } from 'react-icons/cg'
import { FaUser } from 'react-icons/fa6'
import { useAppDispatch, useAppSelector } from 'redux/hooks'
import { getMailHistories } from 'redux/slices/MailHistory'
import * as yup from 'yup'
import { MailApi } from 'API/mailHistory'
import { UIAction } from 'redux/slices/UI'
import BodyEditor from './BodyEditor'
import { LoadingButton } from '@mui/lab'
import SendIcon from '@mui/icons-material/Send'
import { getMailings } from 'redux/slices/Mailing'

interface FormInput {
	mailingIds: string[]
	subject: string
	text: string
	html: string
}

const ITEM_HEIGHT = 48
const ITEM_PADDING_TOP = 8
const MenuProps = {
	PaperProps: {
		style: {
			maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
			width: 250,
		},
	},
}

export default function Mail() {
	const tokenInfo = useAppSelector((s) => s.UI.tokenInfo)
	const status = useAppSelector((s) => s.MailHistory.status)
	const list = useAppSelector((s) => s.MailHistory.list)
	const histories = useMemo(() => {
		return list.map((v) => ({
			...v,
			date: format(new Date(v.createdAt), 'yyyy年MM月dd日'),
			time: format(new Date(v.createdAt), 'HH:mm'),
		}))
	}, [list])
	const dispatch = useAppDispatch()
	useEffect(() => {
		if (status === 'init') dispatch(getMailHistories())
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [])

	const mailingStatus = useAppSelector((s) => s.Mailing.status)
	const mailings = useAppSelector((s) => s.Mailing.list)
	useEffect(() => {
		if (mailingStatus === 'init') dispatch(getMailings())
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [])

	const [selected, setSelected] = useState<ISendMailHistory>()
	const [loading, setLoading] = useState(false)
	const [re_render, setRe_render] = useState(0)

	const schema = yup.object({
		mailingIds: yup.array().required().min(1),
		subject: yup.string().required(),
		text: yup.string().required(),
		html: yup.string().required(),
	})
	const {
		register,
		handleSubmit,
		reset,
		getValues,
		setValue,
		formState: { errors },
	} = useForm<FormInput>({
		resolver: yupResolver(schema),
		defaultValues: {
			subject: '',
			text: '',
			html: '',
			mailingIds: [],
		},
	})

	if (!tokenInfo) return null
	const dateGroup: { [key: string]: typeof histories } = _.groupBy(histories, 'date')

	const onSubmit: SubmitHandler<FormInput> = async ({ html, mailingIds, subject }) => {
		try {
			setLoading(true)
			const { message, error } = await MailApi.send({
				html,
				mailingIds: mailingIds.map((v) => parseInt(v)),
				subject,
			})
			dispatch(
				UIAction.addGlobalMessage({
					message,
					type: error ? 'error' : 'success',
				})
			)
			reset()
			setRe_render(re_render + 1)
		} catch (error) {
		} finally {
			setLoading(false)
		}
	}
	return (
		<Stack
			gap={2}
			direction="row"
			sx={{
				height: '100%',
			}}
		>
			<Stack
				sx={{
					width: 300,
					border: '1px solid #eee',
					borderRadius: 1,
					boxShadow: '1px 2px 10px rgba(0,0,0,0.1)',
					overflow: 'hidden',
					height: '100%',
				}}
			>
				<Typography
					variant="h6"
					sx={{
						bgcolor: (theme) => theme.palette.primary.main,
						color: (theme) => theme.palette.primary.contrastText,
						textAlign: 'center',
						py: 1,
						boxShadow: '0px 2px 4px rgba(0,0,0,0.3)',
					}}
				>
					送信履歴
				</Typography>
				<Stack
					gap={2}
					sx={{
						flex: 1,
						overflow: 'auto',
						p: 0.5,
						boxShadow: 'inset 0 0 10px rgba(0,0,0,0.1)',
					}}
				>
					{Object.keys(dateGroup).map((date, i) => {
						const his = dateGroup[date]
						return (
							<Stack key={i}>
								<Typography variant="caption">{date}</Typography>
								<Divider />
								<Stack gap={1} px={0.5}>
									{his.map((v) => {
										return (
											<Stack
												key={v.id}
												onClick={() => setSelected(v)}
												sx={{
													p: 0.5,
													borderBottom: '1px solid #eee',
													bgcolor: selected?.id === v.id ? '#eee' : 'transparent',
													cursor: 'pointer',
													'&:hover': {
														bgcolor: '#eee8',
													},
												}}
											>
												<Typography
													sx={{
														fontWeight: 'bold',
														width: '100%',
														overflow: 'hidden',
														textOverflow: 'ellipsis',
														whiteSpace: 'nowrap',
														px: 0.5,
													}}
												>
													{v.subject}
												</Typography>
												<Stack direction="row" alignItems="center" gap={2} justifyContent="space-between" px={0.5}>
													<Typography
														variant="caption"
														sx={{
															display: 'flex',
															gap: 0.5,
															alignItems: 'center',
														}}
													>
														<CgOrganisation style={{ fontSize: 14, marginBottom: -2 }} />
														{v.organization}
													</Typography>
													<Typography
														variant="caption"
														sx={{
															display: 'flex',
															gap: 0.5,
															alignItems: 'center',
														}}
													>
														<FaUser style={{ fontSize: 12 }} />
														{v.sender}
													</Typography>
												</Stack>
												<Stack direction="row" alignItems="center" gap={2} justifyContent="flex-end" px={0.5}>
													<Typography
														variant="caption"
														sx={{
															fontStyle: 'italic',
															color: v.status === 'success' ? '#0b1' : '#f20',
														}}
													>
														{v.status} at {v.time}
													</Typography>
												</Stack>
											</Stack>
										)
									})}
								</Stack>
							</Stack>
						)
					})}
				</Stack>
				<Divider />
				<Stack px={1} py={2}>
					<Button
						disabled={!selected || tokenInfo.permissions[PERMISSION.MAIL] < 2}
						variant="contained"
						onClick={() => {
							setSelected(undefined)
							reset()
						}}
					>
						新しいメール
					</Button>
				</Stack>
			</Stack>
			<Stack
				sx={{
					flex: 1,
					border: '1px solid #eee',
					borderRadius: 1,
					boxShadow: '1px 2px 10px rgba(0,0,0,0.1)',
					overflow: 'hidden',
					p: 2,
				}}
			>
				{selected ? (
					<>
						<Stack direction="row" justifyContent="space-between" alignItems="center" py={0.5}>
							<Typography
								sx={{
									fontSize: '1.2rem',
									fontWeight: 'bold',
								}}
							>
								{selected.subject}
							</Typography>

							<Typography variant="caption">
								送信時間：{format(new Date(selected.createdAt), 'yyyy年MM月dd日 HH:mm:ss')}
							</Typography>
						</Stack>
						<Stack direction="row">
							<Typography variant="body2">宛先： </Typography>
							<Typography variant="body2" fontWeight="bold">
								{selected.mailing}
							</Typography>
						</Stack>
						<Box
							component="fieldset"
							sx={{
								p: 1,
								borderRadius: 1.5,
								border: '1px solid',
								borderColor: '#ccc',
								my: 1,
							}}
						>
							<Typography component="legend" variant="body2">
								本文
							</Typography>
							<Stack component="div" dangerouslySetInnerHTML={{ __html: selected.body }}></Stack>
						</Box>
						<Stack
							justifyContent="center"
							alignItems="center"
							sx={{
								height: '3rem',
								background: (theme) =>
									selected.status === 'success' ? theme.palette.success.main : theme.palette.error.main,
							}}
						>
							<Typography
								sx={{
									color: '#fff',
									fontSize: 20,
									fontWeight: 'bold',
									textTransform: 'capitalize',
								}}
							>
								{selected.status}
							</Typography>
						</Stack>
					</>
				) : tokenInfo.permissions[PERMISSION.MAIL] < 2 ? null : (
					<>
						<Typography variant="h6">メール送信内容入力</Typography>
						<Divider />
						<Stack
							gap={2}
							component="form"
							noValidate
							autoComplete="off"
							onSubmit={handleSubmit(onSubmit)}
							sx={{
								p: 1,
								my: 2,
								border: '1px solid #eee',
								borderRadius: 1,
								boxShadow: '1px 2px 10px rgba(0,0,0,0.1)',
							}}
						>
							<TextField
								variant="outlined"
								required
								margin="dense"
								autoFocus
								fullWidth
								InputLabelProps={{
									shrink: true,
								}}
								size="small"
								label={'件名'}
								placeholder="件名を入力してください"
								{...register('subject')}
								error={'subject' in errors}
							/>
							<FormControl fullWidth size="small" required>
								<InputLabel>宛先</InputLabel>
								<Select
									label="宛先"
									value={getValues('mailingIds')}
									multiple
									renderValue={(selected) => {
										return (
											<Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 0.5 }}>
												{selected.map((value) => {
													const mailing = mailings.find((f) => f.id === parseInt(value))
													return <Chip key={value} label={mailing?.name || value} />
												})}
											</Box>
										)
									}}
									MenuProps={MenuProps}
									onChange={(event) => {
										const {
											target: { value },
										} = event
										setValue('mailingIds', typeof value === 'string' ? value.split(',') : value, {
											shouldValidate: true,
										})
									}}
									error={'mailingIds' in errors}
								>
									{mailings.map((mailing) => {
										return (
											<MenuItem key={mailing.id} value={mailing.id}>
												<Checkbox checked={!!getValues('mailingIds').find((f) => `${f}` === `${mailing.id}`)} />
												<ListItemText primary={mailing.name} />
											</MenuItem>
										)
									})}
								</Select>
							</FormControl>
							<BodyEditor
								re_render={re_render}
								setValue={(val) => {
									const elm = document.createElement('div')
									elm.innerHTML = val
									const text = elm.textContent || ''
									setValue('text', text, {
										shouldValidate: true,
									})
									setValue('html', val, {
										shouldValidate: true,
									})
								}}
								error={'text' in errors}
							/>
							<Box>
								<LoadingButton
									variant="outlined"
									disabled={Object.keys(errors).length > 0}
									loading={loading}
									loadingPosition="end"
									endIcon={<SendIcon />}
									type="submit"
								>
									送信する
								</LoadingButton>
							</Box>
						</Stack>
					</>
				)}
			</Stack>
		</Stack>
	)
}
