import { Box, Stack, Typography } from '@mui/material'
import 'katex/dist/katex.min.css'
import { useLayoutEffect } from 'react'
import suneditor from 'suneditor'
import 'suneditor/dist/css/suneditor.min.css'
import ja from 'suneditor/src/lang/ja'
import plugins from 'suneditor/src/plugins'

interface Props {
	error?: boolean
	re_render?: number
	setValue: (val: string) => void
}
export default function BodyEditor({ setValue, error, re_render }: Props) {
	useLayoutEffect(() => {
		const div = document.createElement('div')
		document.getElementById('honbun')!.appendChild(div)
		const ed = suneditor.create(div, {
			mode: 'inline',
			lang: {
				...ja,
				toolbar: {
					...ja.toolbar,
					align: '配置',
				},
			},
			placeholder: '本文を入力してください',
			display: 'block',
			popupDisplay: 'full',
			defaultStyle: 'font-size:16px;padding:0;',
			tagsBlacklist: 'script|link|meta',
			pasteTagsBlacklist: 'script|link|meta',
			stickyToolbar: '0',
			plugins: { ...plugins },
			minHeight: '100px',
			height: '100%',
			width: '100%',
			fontSize: [8, 10, 12, 14, 16, 18, 20, 24, 36, 48],
			fontSizeUnit: 'px',
			buttonList: [
				['undo', 'redo'],
				['fontSize', 'align', 'fontColor', 'hiliteColor'],
				['bold', 'underline', 'italic'],
				['link'],
				['removeFormat'],
			],
		})
		ed.onChange = (contents) => {
			setValue(contents)
		}
		return () => {
			ed.destroy()
		}
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [re_render])
	return (
		<Stack
			gap={1}
			sx={{
				width: '100%',
				'& p': {
					m: '0 !important',
				},
			}}
		>
			<Box
				component="fieldset"
				sx={{
					px: 2,
					py: 0,
					borderRadius: 1.5,
					border: '1px solid',
					borderColor: error ? '#f10' : '#ccc',
					color: error ? '#f10' : 'currentColor',
					'& .sun-editor': {
						border: 'none !important',
					},
					'.sun-editor .se-wrapper .se-placeholder': {
						padding: 0,
					},
				}}
			>
				<Typography component="legend" variant="body2">
					本文
				</Typography>
				<Stack
					id="honbun"
					sx={{
						minHeight: 100,
						cursor: 'text',
					}}
				></Stack>
			</Box>
		</Stack>
	)
}
